.autoComplete .MuiAutocomplete-inputRoot.MuiInputBase-fullWidth{
  height: 78px;
  padding-right: 20px!important;
  padding-left: 20px!important;
  box-shadow: 0 5px 30px rgba(156,160,171,.25)!important;
}
.btnPrimary{
  background-color: #008489!important;
  text-transform: capitalize!important;
}
.CardHover .MuiCardMedia-root{
  transition: 0.5s ease-in-out;
}
.CardHover:hover .MuiCardMedia-root{
transform: scale(1.1);
transition: 0.5s ease-in-out;
}
.btnSecondary{
  color:#008489!important;
  text-transform: capitalize!important;
}
/* owl style */
.owl-nav{
  position: absolute;
    top: 40%;
    width: 100%;
}
.owl-nav .owl-prev{
  position: absolute;
  background: #fff;
  left: -40px;
  border-radius: 50%!important;
  border: 1px solid #000!important;
  height: 26px;
}
.owl-nav .owl-next{
  position: absolute;
  background: #fff;
  right: -40px;
  border-radius: 50%!important;
  border: 1px solid #000!important;
  height: 26px;
}
.owl-nav .owl-prev,.owl-nav .owl-next{
  box-shadow: none;
  outline: none;
}
.owl-nav .owl-prev:hover,.owl-nav .owl-next:hover {
  border-color: #008489!important;
  background: #fff!important;
}
.owl-nav .owl-prev:hover .pathHover,.owl-nav .owl-next:hover .pathHover{
fill: #008489!important;
}
.owlPadding{
  padding:10px 2px;
}
.MuiListItem-gutters{
  padding: 3px 8px!important;
}
.MuiListItem-gutters .MuiListItemText-root .MuiListItemText-primary{
  font-size: 14px;
  color:#484848;
  font-weight: 500;
}
.facebook svg,.google svg,.twitter svg, .youtube svg, .linkedin svg, .pinterest svg, .instagram svg{
  fill:rgba(0, 0, 0, 0.54);
  transition: 0.4s ease-in-out;
}
.facebook:hover svg{
  fill: #1675eb;
  transition: 0.4s ease-in-out;
}
.twitter:hover svg{
  fill: #56b2e7;
  transition: 0.4s ease-in-out;
}
.google:hover svg{
  fill: #db4a39;
  transition: 0.4s ease-in-out;
}
.youtube:hover svg{
  fill: #d92b26;
  transition: 0.4s ease-in-out;
}
.linkedin:hover svg{
  fill: #0e72a3;
  transition: 0.4s ease-in-out;
}
.pinterest:hover svg{
  fill: #cb1f27;
  transition: 0.4s ease-in-out;
}
.instagram:hover svg{
  fill: #ac2cb1;
  transition: 0.4s ease-in-out;
}
.float{
  float: right;
}
.react-daterange-picker__wrapper{
  display: none!important;
}
.react-daterange-picker{
    position: absolute!important;
    left: 24px;
    top: 36px;
}
.menuWidth .MuiMenu-paper.MuiPopover-paper{
  width: 200px;
  padding: 0px 10px;
  margin-top: 88px;
}
.menuwidthpadding .MuiMenu-list.MuiList-padding{
  padding: 40px 0px!important;
}
.datepickerhome{
  position: absolute;
  left: 16px!important;
  top: 62px!important;
}
.react-calendar__tile--range{
  background-color: #008489!important;
}
.react-calendar{
  border-color: #e4e4e4!important;
  box-shadow: 0 5px 30px rgba(156,160,171,.25)!important;
}
.react-calendar__tile{
  margin-top: 8px!important;
}
.react-calendar__tile--rangeStart,.react-calendar__tile--hoverStart{
  border-radius: 20px 0px 0px 20px!important;
  background-color: #008489!important;
}
.react-calendar__tile--hover{
  background-color: rgba(0, 132, 137, 0.32)!important;
}
.react-calendar__tile--rangeEnd,.react-calendar__tile--hoverEnd{
  border-radius: 0px 20px 20px 0px;
  background-color: #008489!important;
}
